	var path = 'pages/components/carousel/carousel.html';
	var html = "<div class=markdown-body><h1 id=carousel->Carousel 走马灯</h1> <p>旋转木马，一组轮播的区域。</p> <h2 id=->何时使用</h2> <ul> <li>当有一组平级的内容。</li> <li>当内容空间不足时，可以用走马灯的形式进行收纳，进行轮播展现。</li> <li>常用于一组图片或卡片轮播。</li> </ul> <h2 id=->如何使用</h2> <p><code>Carousel</code>组件支持手机端，只需添加 <code>ngTouch</code>依赖模块</p> <p>使用一个<code>&lt; ui-carousel &gt;</code>标签包含<code>&lt; ui-slide &gt;</code>在里面。</p> <h2 id=->属性</h2> <table> <thead> <tr> <th>成员</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>active</td> <td>将改slide作为当前活动页</td> <td>boolean</td> <td>-</td> </tr> <tr> <td>interval</td> <td>自动循环播放间隔时间，大于0生效</td> <td>integer</td> <td>0</td> </tr> <tr> <td>no-pause</td> <td>默认情况下鼠标悬停在图片上会停止播放，此属性可禁用改控制</td> <td>boolean</td> <td><code>false</code></td> </tr> <tr> <td>no-transition</td> <td>否要禁用幻灯片之间的过渡动画。设置 true 则禁用这个过渡动画</td> <td>boolean</td> <td><code>false</code></td> </tr> <tr> <td>no-wrap</td> <td>禁用幻灯片的循环</td> <td>boolean</td> <td><code>false</code></td> </tr> <tr> <td>template-url</td> <td>自定义模板路径</td> <td>string</td> <td>-</td> </tr> <tr> <td>actual</td> <td>绑定slide mode到slide scope作用域，在自定义template模板的时候可以使用</td> <td>string</td> <td><code>none</code></td> </tr> <tr> <td>index</td> <td>slide的索引，必须是唯一的。</td> <td>string</td> <td><code>none</code></td> </tr> </tbody> </table> <h2 id=->代码演示</h2> <div class=bs-example><br> <div> <div style=height:405px> <div ui-carousel=\"\" active=ctrl.active interval=ctrl.myInterval no-wrap=ctrl.noWrapSlides> <div ui-slide=\"\" ng-repeat=\"slide in ctrl.slides track by slide.id\" index=slide.id> <img ng-src={{slide.image}} style=margin:auto> <div class=carousel-caption> <h4>Slide {{slide.id}}</h4> <p>{{slide.text}}</p> </div> </div> </div> </div> <div class=row> <div class=col-md-6> <button type=button class=\"btn btn-info\" ng-click=ctrl.addSlide(1)>添加Slide</button> <button type=button class=\"btn btn-info\" ng-click=ctrl.randomize()>重新排序Slide</button> <div class=checkbox> <label> <input type=checkbox ng-model=ctrl.noWrapSlides> 禁用循环播放 </label> </div> </div> <div class=col-md-6> Interval, in milliseconds: <input type=number class=form-control ng-model=ctrl.myInterval> <br> 输入一个负数或0则无效，会停止自动播放 </div> </div> </div> </div> <ui-clipboard clipboard-target=clipboard3></ui-clipboard> <div class=highlight id=clipboard3> <pre><code style=width:100%;color:#fff><span class=xml>\n<span class=hljs-tag>&lt;<span class=hljs-name>div</span>&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>style</span>=<span class=hljs-string>&quot;height: 405px&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>ui-carousel</span>=<span class=hljs-string>&quot;&quot;</span> <span class=hljs-attr>active</span>=<span class=hljs-string>&quot;ctrl.active&quot;</span> <span class=hljs-attr>interval</span>=<span class=hljs-string>&quot;ctrl.myInterval&quot;</span> <span class=hljs-attr>no-wrap</span>=<span class=hljs-string>&quot;ctrl.noWrapSlides&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>ui-slide</span>=<span class=hljs-string>&quot;&quot;</span> <span class=hljs-attr>ng-repeat</span>=<span class=hljs-string>&quot;slide in ctrl.slides track by slide.id&quot;</span> <span class=hljs-attr>index</span>=<span class=hljs-string>&quot;slide.id&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>img</span> <span class=hljs-attr>ng-src</span>=<span class=hljs-string>&quot;</span></span></span><span class=hljs-template-variable>{{slide.image}}</span><span class=xml><span class=hljs-tag><span class=hljs-string>&quot;</span> <span class=hljs-attr>style</span>=<span class=hljs-string>&quot;margin:auto;&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;carousel-caption&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>h4</span>&gt;</span>Slide </span><span class=hljs-template-variable>{{slide.id}}</span><span class=xml><span class=hljs-tag>&lt;/<span class=hljs-name>h4</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>p</span>&gt;</span></span><span class=hljs-template-variable>{{slide.text}}</span><span class=xml><span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n  <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;row&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;col-md-6&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;button&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;btn btn-info&quot;</span> <span class=hljs-attr>ng-click</span>=<span class=hljs-string>&quot;ctrl.addSlide(1)&quot;</span>&gt;</span>添加Slide<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;button&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;btn btn-info&quot;</span> <span class=hljs-attr>ng-click</span>=<span class=hljs-string>&quot;ctrl.randomize()&quot;</span>&gt;</span>重新排序Slide<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;checkbox&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>label</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>input</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;checkbox&quot;</span> <span class=hljs-attr>ng-model</span>=<span class=hljs-string>&quot;ctrl.noWrapSlides&quot;</span>&gt;</span>\n          禁用循环\n        <span class=hljs-tag>&lt;/<span class=hljs-name>label</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;col-md-6&quot;</span>&gt;</span>\n      Interval, in milliseconds: <span class=hljs-tag>&lt;<span class=hljs-name>input</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;number&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;form-control&quot;</span> <span class=hljs-attr>ng-model</span>=<span class=hljs-string>&quot;ctrl.myInterval&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>br</span>&gt;</span> 输入一个负数或0则无效，会停止自动播放\n    <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n  <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n</span></code></pre></div> </div>";
	window.angular.module('ng').run(['$templateCache', function(c) { c.put(path, html) }]);
	module.exports = path;

