	var path = 'pages/components/datepicker/datepicker.html';
	var html = "<div class=markdown-body><h1 id=datepicker->DatePicker 日期选择框</h1> <p>输入或选择日期的控件。</p> <h2 id=->何时使用</h2> <p>当用户需要输入一个日期，可以点击标准输入框，弹出日期面板进行选择。</p> <h2 id=->如何使用</h2> <p>将指令<code>&lt; ui-datepicker &gt;</code>添加到任意元素，绑定日期选择组件。在<code>format</code>属性中设置日期显示格式。</p> <p>在<code>view</code>属性中设置选择器的功能类型，共有5中类型：</p> <ul> <li>date: 日期选择器</li> <li>datetime: 日期时间选择器</li> <li>time: 时间选择器</li> <li>year: 年份选择</li> <li>month: 月份选择</li> </ul> <h2 id=->属性</h2> <table> <thead> <tr> <th>成员</th> <th>默认值</th> <th>描述</th> </tr> </thead> <tbody> <tr> <td>ng-model</td> <td><code>undefined</code></td> <td>绑定对象</td> </tr> <tr> <td>view</td> <td><code>undefined</code></td> <td>选择器模式，可选值: <code>date</code>, <code>datetime</code>, <code>time</code>, <code>year</code>, <code>month</code></td> </tr> <tr> <td>format</td> <td><code>undefined</code></td> <td>显示格式，如: <code>yyyy-MM-dd HH:mm:ss</code></td> </tr> <tr> <td>min-date</td> <td><code>undefined</code></td> <td>最小可选日期</td> </tr> <tr> <td>max-date</td> <td><code>undefined</code></td> <td>最大可选日期</td> </tr> <tr> <td>disabled-date(date)</td> <td><code>undefined</code></td> <td>date为时间戳</td> </tr> <tr> <td>auto-close</td> <td><code>true</code></td> <td>当view为date, year, month时是否选择后自动关闭</td> </tr> </tbody> </table> <h2 id=->代码演示</h2> <h3 id=-5->日期选择器 5 中类型</h3> <div class=bs-example> <p>选择日期: <em>{{ctrl.date}}</em></p> <div class=row> <div class=col-md-4> <ui-datepicker ng-model=ctrl.date view=date format=yyyy-MM-dd> </ui-datepicker></div> </div> <br> <p>选择日期时间: <em>{{ctrl.datetime}}</em></p> <div class=row> <div class=col-md-4> <ui-datepicker ng-model=ctrl.datetime view=datetime format=\"yyyy-MM-dd HH:mm:ss\"> </ui-datepicker></div> </div> <br> <p>选择时间: <em>{{ctrl.time}}</em></p> <div class=row> <div class=col-md-4> <ui-datepicker ng-model=ctrl.time view=time format=HH:mm:ss> </ui-datepicker></div> </div> <br> <p>选择年份: <em>{{ctrl.year}}</em></p> <div class=row> <div class=col-md-4> <ui-datepicker ng-model=ctrl.year view=year format=yyyy> </ui-datepicker></div> </div> <br> <p>选择月份: <em>{{ctrl.month}}</em></p> <div class=row> <div class=col-md-4> <ui-datepicker ng-model=ctrl.month view=month format=MM> </ui-datepicker></div> </div> </div> <ui-clipboard clipboard-target=clipboard1></ui-clipboard> <div class=highlight id=clipboard1> <pre><code style=width:100%;color:#fff>\n&lt;p&gt;选择日期: <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>em</span>&gt;</span>{{ctrl.date}}<span class=hljs-tag>&lt;/<span class=hljs-name>em</span>&gt;</span></span><span class=xml><span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span></span>\n&lt;div <span class=hljs-class><span class=hljs-keyword>class</span></span>=<span class=hljs-string>&quot;row&quot;</span>&gt;\n    <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;col-md-4&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>ui-datepicker</span> <span class=hljs-attr>ng-model</span>=<span class=hljs-string>&quot;ctrl.date&quot;</span> <span class=hljs-attr>view</span>=<span class=hljs-string>&quot;date&quot;</span> <span class=hljs-attr>format</span>=<span class=hljs-string>&quot;yyyy-MM-dd&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>ui-datepicker</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span></span>\n&lt;br&gt;\n<span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>p</span>&gt;</span>选择日期时间: <span class=hljs-tag>&lt;<span class=hljs-name>em</span>&gt;</span>{{ctrl.datetime}}<span class=hljs-tag>&lt;/<span class=hljs-name>em</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span></span>\n&lt;div <span class=hljs-class><span class=hljs-keyword>class</span></span>=<span class=hljs-string>&quot;row&quot;</span>&gt;\n    <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;col-md-4&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>ui-datepicker</span> <span class=hljs-attr>ng-model</span>=<span class=hljs-string>&quot;ctrl.datetime&quot;</span> <span class=hljs-attr>view</span>=<span class=hljs-string>&quot;datetime&quot;</span> <span class=hljs-attr>format</span>=<span class=hljs-string>&quot;yyyy-MM-dd HH:mm:ss&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>ui-datepicker</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span></span>\n&lt;br&gt;\n<span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>p</span>&gt;</span>选择时间: <span class=hljs-tag>&lt;<span class=hljs-name>em</span>&gt;</span>{{ctrl.time}}<span class=hljs-tag>&lt;/<span class=hljs-name>em</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span></span>\n&lt;div <span class=hljs-class><span class=hljs-keyword>class</span></span>=<span class=hljs-string>&quot;row&quot;</span>&gt;\n    <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;col-md-4&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>ui-datepicker</span> <span class=hljs-attr>ng-model</span>=<span class=hljs-string>&quot;ctrl.time&quot;</span> <span class=hljs-attr>view</span>=<span class=hljs-string>&quot;time&quot;</span> <span class=hljs-attr>format</span>=<span class=hljs-string>&quot;HH:mm:ss&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>ui-datepicker</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span></span>\n&lt;br&gt;\n<span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>p</span>&gt;</span>选择年份: <span class=hljs-tag>&lt;<span class=hljs-name>em</span>&gt;</span>{{ctrl.year}}<span class=hljs-tag>&lt;/<span class=hljs-name>em</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span></span>\n&lt;div <span class=hljs-class><span class=hljs-keyword>class</span></span>=<span class=hljs-string>&quot;row&quot;</span>&gt;\n    <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;col-md-4&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>ui-datepicker</span> <span class=hljs-attr>ng-model</span>=<span class=hljs-string>&quot;ctrl.year&quot;</span> <span class=hljs-attr>view</span>=<span class=hljs-string>&quot;year&quot;</span> <span class=hljs-attr>format</span>=<span class=hljs-string>&quot;yyyy&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>ui-datepicker</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span></span>\n&lt;br&gt;\n<span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>p</span>&gt;</span>选择月份: <span class=hljs-tag>&lt;<span class=hljs-name>em</span>&gt;</span>{{ctrl.month}}<span class=hljs-tag>&lt;/<span class=hljs-name>em</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span></span>\n&lt;div <span class=hljs-class><span class=hljs-keyword>class</span></span>=<span class=hljs-string>&quot;row&quot;</span>&gt;\n    <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;col-md-4&quot;</span>&gt;</span>\n       <span class=hljs-tag>&lt;<span class=hljs-name>ui-datepicker</span> <span class=hljs-attr>ng-model</span>=<span class=hljs-string>&quot;ctrl.month&quot;</span> <span class=hljs-attr>view</span>=<span class=hljs-string>&quot;month&quot;</span> <span class=hljs-attr>format</span>=<span class=hljs-string>&quot;MM&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>ui-datepicker</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span></span>\n</code></pre> </div> <h3 id=->禁止选择特定日期</h3> <p>设置<code>disabled-date(date)</code>属性，设置自定义的方法对指定的日期进行过滤，如果方法的返回值为<code>true</code>时日期将被禁用。</p> <p>参数<code>date</code>格式为时间戳</p> <div class=bs-example> <div class=row> <div class=col-md-4> <ui-datepicker ng-model=ctrl.datetime view=datetime format=\"yyyy-MM-dd HH:mm:ss\" disabled-date=ctrl.disabled(date)> </ui-datepicker></div> </div> </div> <ui-clipboard clipboard-target=clipboard2></ui-clipboard> <div class=highlight id=clipboard2> <pre><code style=width:100%;color:#fff>\n&lt;div <span class=hljs-class><span class=hljs-keyword>class</span></span>=<span class=hljs-string>&quot;row&quot;</span>&gt;\n    <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;col-md-4&quot;</span>&gt;</span>\n       <span class=hljs-tag>&lt;<span class=hljs-name>ui-datepicker</span> <span class=hljs-attr>ng-model</span>=<span class=hljs-string>&quot;ctrl.datetime&quot;</span> <span class=hljs-attr>view</span>=<span class=hljs-string>&quot;datetime&quot;</span> <span class=hljs-attr>format</span>=<span class=hljs-string>&quot;yyyy-MM-dd HH:mm:ss&quot;</span> <span class=hljs-attr>disabled-date</span>=<span class=hljs-string>&quot;ctrl.disabled(date)&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>ui-datepicker</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span></span>\n</code></pre> </div> <h3 id=-date-picker->此外，提供 <code>&lt; date-picker &gt;</code>指令，可用来创建日期面板</h3> <h4 id=->日历面板</h4> <div class=bs-example> <div class=row> <div class=col-md-4> <date-picker ng-model=ctrl.date view=date format=yyyy-MM-dd> </date-picker></div> </div> </div> <ui-clipboard clipboard-target=clipboard4></ui-clipboard> <div class=highlight id=clipboard4> <pre><code style=width:100%;color:#fff>\n&lt;<span class=hljs-keyword>div</span> <span class=hljs-built_in>class</span>=<span class=hljs-string>&quot;row&quot;</span>&gt;\n    &lt;<span class=hljs-keyword>div</span> <span class=hljs-built_in>class</span>=<span class=hljs-string>&quot;col-md-4&quot;</span>&gt;\n       &lt;<span class=hljs-built_in>date</span>-picker ng-model=<span class=hljs-string>&quot;ctrl.date&quot;</span> view=<span class=hljs-string>&quot;date&quot;</span> format=<span class=hljs-string>&quot;yyyy-MM-dd&quot;</span>&gt;\n    &lt;/<span class=hljs-built_in>date</span>-picker&gt;&lt;/<span class=hljs-keyword>div</span>&gt;\n&lt;/<span class=hljs-keyword>div</span>&gt;\n</code></pre> </div> <h4 id=->月份面板</h4> <div class=bs-example> <div class=row> <div class=col-md-4> <date-picker ng-model=ctrl.date view=month format=MM> </date-picker></div> </div> </div> <ui-clipboard clipboard-target=clipboard5></ui-clipboard> <div class=highlight id=clipboard5> <pre><code style=width:100%;color:#fff>\n&lt;<span class=hljs-keyword>div</span> <span class=hljs-built_in>class</span>=<span class=hljs-string>&quot;row&quot;</span>&gt;\n    &lt;<span class=hljs-keyword>div</span> <span class=hljs-built_in>class</span>=<span class=hljs-string>&quot;col-md-4&quot;</span>&gt;\n       &lt;<span class=hljs-built_in>date</span>-picker ng-model=<span class=hljs-string>&quot;ctrl.date&quot;</span> view=<span class=hljs-string>&quot;month&quot;</span> format=<span class=hljs-string>&quot;MM&quot;</span>&gt;\n    &lt;/<span class=hljs-built_in>date</span>-picker&gt;&lt;/<span class=hljs-keyword>div</span>&gt;\n&lt;/<span class=hljs-keyword>div</span>&gt;\n</code></pre> </div></div>";
	window.angular.module('ng').run(['$templateCache', function(c) { c.put(path, html) }]);
	module.exports = path;

